              href="https://wa.me/27727001800?text=Hi%2C%20I%20need%20urgent%20assistance%20please"
              className="bg-green-600 hover:bg-green-700 text-white font-semibold py-4 px-12 rounded-xl text-lg shadow-xl transition-all hover:scale-105"
              aria-label="Contact via WhatsApp for 24/7 emergency help"
